import { _decorator } from 'cc';
import mapData from '../data/map.json';
const { ccclass, property } = _decorator;

@ccclass('MapData')
export class MapData {

    maxLow: number = 0;
    maxCol: number = 0;

    map = {
        id: '',
        low: 0,
        col: 0,
        map: [],
    };

    constructor() {
    }

    getMapFromID(id: string) {
        this.map = mapData[id];
        return this.map;
    }
}

